@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Text:wght@200&display=swap);
* {
  border: 0;
  padding: 0;
  margin: 0;
  vertical-align: baseline;
  box-sizing: border-box;
  font-weight: inherit;
  font-family: inherit;
  font-style: inherit;
  font-size: 100%;
}

:root {
  /*Fonts: */
  --default-font: "Open Sans Condensed";
  --secondary-font: "Big Shoulders Text";
  /*Colors*/
  --primary: #000814;
  --secondary: #003566;
  --secondary-dark: #001d3d;
  --red: #d00000;
  --background: #d9d9d9;
}

body {
  background-color: #d9d9d9;
  background-color: var(--background);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    "Big Shoulders Text", "Open Sans Condensed", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  width: 100%;
  height: auto;
}

.productField {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

@media (min-width: 1920px) and (max-width: 2048px) {
  .productField {
    justify-content: flex-start;
  }
}

.btn {
  width: 10rem;
  height: 2rem;
  background-color: var(--secondary);
  border-radius: 0.2rem;
  font: 1.3rem var(--default-font);
  color: #ffff;
  cursor: pointer;
  transition: ease 0.2s;
}

.btn:hover {
  background-color: var(--red);
}

#btn--finish {
  background-color: var(--primary);
}

#btn--finish:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.product {
  display: flex;
  width: 24rem;
  height: 12rem;
  background-color: #ffff;
  padding: 0.5rem;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.4);
  color: var(--secondary-dark);
  border-radius: 0.2rem;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  transition: all ease 0.2s;
  border-right: 3px solid var(--secondary);
}

.product__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 11rem;
}

.product__title {
  font: 1.3rem var(--default-font);
}

.product__price {
  font: 1.5rem var(--default-font);
  font-weight: 700;
}

.product:hover {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.overlay {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #00356665;
  z-index: 1;
}

.overlay--none {
  display: none;
}

.cartField {
  width: 20rem;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 2;
  background: #ffff;
  border-left: 3px solid var(--secondary);
  display: flex;
  flex-direction: column;
  align-items: center;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  overflow: hidden;
  transition: all ease 0.6s;
}

.cartField--hidden {
  width: 20rem;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 2;
  background: #ffff;
  border-left: 3px solid var(--secondary);
  display: flex;
  flex-direction: column;
  align-items: center;
  -webkit-transform: translateX(21rem);
          transform: translateX(21rem);
  overflow: hidden;
  transition: all ease 0.6s;
}

.cartHeader {
  width: 100%;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.5rem;
  background-color: var(--secondary);
}

.cartHeader__title {
  font: 1.2rem var(--default-font);
  font-weight: 700;
  color: #ffff;
}

.cartHeader__closeIcon {
  width: 24px;
  height: 24px;
  cursor: pointer;
}

.cartField__products {
  width: 100%;
  height: calc(100vh - 9rem);
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
  overflow-x: hidden;
}

.checkout {
  width: 100%;
  height: 6rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.5rem;
  background-color: var(--secondary);
}

.checkout__info {
  width: 15rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.checkout__info h1 {
  font: 1.1rem var(--default-font);
  font-weight: 700;
  color: #ffff;
}

@media (max-width: 767px) {
  .cartField {
    width: 100%;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  .cartField--hidden {
    width: 100%;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}

.cartProduct {
  width: 18.5rem;
  height: 4rem;
  display: grid;
  grid-template-columns: 17% 8% 45% 30%;
  grid-template-rows: auto;
  grid-template-areas: "image quantity title price";
  align-items: center;
  border: 1px solid var(--primary);
  border-radius: 0.2rem;
  margin: 0.2rem 0;
  background-color: #ffff;
  grid-gap: 0.4rem;
  gap: 0.4rem;
  position: relative;
}

.cartProduct__image {
  width: 3.5rem;
  height: 3.5rem;
  grid-area: image;
}

.cartProduct__title {
  grid-area: title;
  font: 1.1rem var(--default-font);
}

.cartProduct__price {
  grid-area: price;
  font: 1.1rem var(--default-font);
  font-weight: 700;
}

.cartProduct__quantity {
  grid-area: quantity;
  font: 1.1rem var(--default-font);
  font-weight: 700;
  justify-self: center;
}

.cartProduct__remove {
  position: absolute;
  top: -0.2rem;
  right: 0.2rem;
  font: 1.2rem var(--default-font);
  font-weight: 700;
  cursor: pointer;
  transition: 0.2s;
}

.cartProduct__remove:hover {
  color: var(--red);
}

.header {
  width: 100%;
  height: 4rem;
  background-color: var(--primary);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#headerPipe--green {
  box-shadow: inset -6px 0px 0px 0px #2dc653;
  transition: all ease 0.5s;
}

#headerPipe--red {
  box-shadow: inset -6px 0px 0px 0px var(--red);
  transition: all ease 0.5s;
}
.header__title {
  font: 2.5rem var(--secondary-font);
  color: #ffff;
  margin-left: 0.8rem;
}

.header__title--red {
  color: var(--red);
}

.header__cart {
  height: 2rem;
  width: 2.5rem;
  cursor: pointer;
  margin-right: 0.8rem;
}

.order {
  height: 2.5rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font: 1.3rem var(--default-font);
  background-color: var(--secondary);
  border-bottom: 2px solid var(--primary);
}

.order__select {
  width: 8rem;
  text-align: center;
  outline: none;
  border: 1px solid var(--primary);
}

